.App-logo {
  /* height: 40vmin; */
  height: 8vw;
  pointer-events: none;
}

.page-image {
  height: 200px;
  width: auto;
  /* margin-right: 20px; */
  display:block;
  margin-left:auto;
  margin-right:auto;
  display: block;
}

.page-image-container {
  margin-top: 20px;
  margin-bottom: 20px;
  align-items: center;
}

.main-title {
  font-family: 'Poppins', sans-serif;
  font-size: 8vw;
  margin: 0px;
  display: inline-block;
}

.title-icon {
  font-family: monospace, monospace;
  /* font-weight: bold; */
  color: rgb(97, 219, 251);
  margin-left: 0px;
}


.title-and-logo {
  align-items: left;
  display: flex;
  /* animation: 1s ease-out 0s 1 slideInTop; */
  margin-left: 2vw;
  column-span: all;
}

.body-container {
  margin: 0px;
  margin-top: 25px;
  margin-left: 2vw; /* for when super zoomed in */
  flex-direction: column;
  display: flex;
}

.body-text {
  margin-bottom: 30px;
  max-width: 800px;
}

.right-aligned {
  text-align: right;
}

#logo-wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  overflow:hidden;
  transition: 0.5s;
  cursor: pointer;
}

#logo-wrapper:hover {
  transform: scale(1.25);
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;

  /* width: 100%; */
}

.main-content {
  z-index: 10;
  position: relative; 
  margin-left: 1.5vw;
}

.background-obj {
  background: linear-gradient(to left, cyan 50%, purple 50%);
  filter: blur(10vw);
  width: 20vw;
  height: 20vw;
  left: 40vw;
  top: 20vh;
  position: fixed;

  animation: spin infinite 15s;
  /* animation-timing-function: ease-in-out; */
  animation-timing-function: linear;
}

@media screen and (min-width: 961px) {
  .main-title {
    font-size: 100px;
  }
  .App-logo {
    height: 80px;
  }
  gradient-text {
    font-size: 32px;
  }
  .body-container {
    /* font-size: 24px; */
    width: 800px;
  }
}

@media screen and (max-width: 960px) {
  .main-title {
    font-size: 10vw;
  }
  gradient-text {
    font-size: 4vw;
  }
  .App-logo {
    height: 10vw;
  }
  .body-container {
    /* font-size: 16px; */
    width: 90%;
  }
}

/* @media screen and (width < height) {
  .main-title {
    font-size: 2vw;
  }
} */

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slideInTop {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes pulseWidth { 
  0% {
    width: 20vw;
    left: 40vw;
  }
  50% {
    width: 50vw;
    left: 25vw;
  }
  100% {
    width: 20vw;
    left: 40vw;
  }
}

@keyframes figureEight {
  0% {
    left: 50vw;
    top: 20vh;
  }
  12.5% {
    left: 40vw;
    top: 10vh;
  }
  25% {
    left: 30vw;
    top: 20vh;
  }
  37.5% {
    left: 40vw;
    top: 30vh;
  }
  50% {
    left: 50vw;
    top: 20vh;
  }
  62.5% {
    left: 60vw;
    top: 10vh;
  }
  75% {
    left: 70vw;
    top: 20vh;
  }
  87.5% {
    left: 60vw;
    top: 30vh;
  }
  100% {
    left: 50vw;
    top: 20vh;
  }
}